import {Component} from '@angular/core';
import {DashboardComponent} from './+dashboard';
import {Routes, ROUTER_DIRECTIVES, ROUTER_PROVIDERS} from '@angular/router';
import {ClientsComponent} from './+clients';
import {HomeComponent} from './+home';
import {ClientbriefComponent} from './+clientbrief';
import {MediaplanComponent} from './+mediaplan';
import {MediaplanSummaryComponent} from './+mediaplan/+mediaplan-summary';
import {ConceptsComponent} from './+concepts';
import { SuggestionComponent } from './+suggestion';
import {MriquestionComponent} from './+mriquestion';
import {PlanprogressComponent} from './planprogress/planprogress.component';
import {OutputComponent} from './output';

@Component({
    moduleId: module.id,
    selector: 'media-planner-app',
    templateUrl: 'media-planner.component.html',
    styleUrls: ['media-planner.component.css'],
    directives: [ROUTER_DIRECTIVES,PlanprogressComponent],
    providers: [ROUTER_PROVIDERS]
})
@Routes([
    {path: '/', component: HomeComponent},
    {path: '/dashboard', component: DashboardComponent},
    {path: '/clients', component: ClientsComponent},
    {path: '/clientbrief', component: ClientbriefComponent},
    {path: '/mediaplan', component: MediaplanComponent},
    {path: '/concepts', component: ConceptsComponent},
    {path: '/suggestion', component: SuggestionComponent},
    {path: '/mriquestion', component: MriquestionComponent},
    {path: '/output', component: OutputComponent}
])
export class MediaPlannerAppComponent {
    title = 'media-planner works!';
}
